    --light-clr: #fff7f5;
    --dark-clr: hsl(329, 100%, 11%);
    --accent-clr: hsl(356, 89%, 45%);
    --light-pink-clr: #ffd5cc;
    --content-width: min(100%, 950px);
    --content-padding: 25px 50px;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

body {
    font-family: "Open Sans", Helvetica, sans-serif;
    background-color: var(--light-clr);
    text-wrap: pretty;
    display: flex;
    flex-direction: column;
}

main {
    flex: 1;
}

h1 {
    font-size: 3rem;
    line-height: 4.2rem;
    margin: 0;
}

h2 {
    font-size: 1.5rem;
    font-weight: 700;
    text-align: center;
    color: var(--dark-clr);
}

p {
    line-height: 1.3rem;
    font-size: 0.8rem;
}

button {
    cursor: pointer;
    border: none;
    outline: none;
    font: inherit;
    color: inherit;
}

figure img {
    border-radius: 50px;
    border: 5px solid var(--light-pink-clr);
}

/* header */
header {
    background-color: var(--dark-clr);
}

header .inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: var(--content-width);
    margin: 0 auto;
    padding: var(--content-padding);
    color: var(--light-clr);
}

.logo {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: contain;
    background-repeat: no-repeat;
    width: 120px;
    height: 60px;
    cursor: pointer;
}

nav {
    display: flex;
    gap: 30px;
}

nav button {
    background: transparent;
}

nav button.selected {
    color: var(--accent-clr);
    text-decoration: underline var(--accent-clr);
    text-underline-offset: 5px;
}

/* hero */
.hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    padding-bottom: 80px;
    background-color: var(--dark-clr);
    color: var(--light-pink-clr);
}

.hero .flex {
    width: var(--content-width);
    padding: var(--content-padding);
    margin: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 25px;
}

.hero .text {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    width: 100%;
}

.hero h1 {
    flex: 2;
    margin: 0;
}

.hero p {
    flex: 1;
    text-align: right;

    @media (max-width: 450px) {
        display: none;
    }
}

.hero .cta-container {
    width: 100%;
}

.hero .cta {
    border: none;
    outline: none;
    padding: 18px 50px;
    font-weight: 700;
    border-radius: 50px;
    background-color: var(--accent-clr);
    color: white;
    text-wrap: nowrap;
}

.hero .cta:hover {
    filter: brightness(1.05);
}

.hero .cta:active {
    filter: brightness(.98);
    transform: scale(.99);
}

.hero figure {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 25px;
}

.hero figure img {
    flex: 1;
}

.hero .centre {
    flex: 3;
}

/* main section */
#steps,
#locations,
#ingredients,
#bases {
    width: var(--content-width);
    margin: 0 auto;
    padding: 50px;
}

.section-subtext {
    text-align: center;
}

#steps {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 35px;
}

#steps>.grid {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
}

#steps .step {
    display: flex;
    flex-direction: column;
    align-items: center;
    line-height: 1.75rem;
    gap: 20px;
    padding: 20px 25px;
}

#steps .step:not(:last-child) {
    border-right: 3px solid var(--light-pink-clr);

    @media (max-width: 699px) {
        border: none;
    }
}

#steps .text {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-self: flex-start;
    text-align: left;
    gap: 25px;
}

#steps img {
    width: 65px;
}

#locations {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 35px;
}

#locations>.grid {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(275px, 1fr));
    gap: 50px;
}

#locations .card {
    background-color: hsl(0, 0%, 100%);
    border-radius: 25px;
    border: 3px solid var(--light-pink-clr);
    padding: 25px 35px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}

#locations .card h3 {
    text-align: center;
    color: var(--dark-clr);
}

#locations .card .location-photo {
    width: 100%;
    margin-bottom: 20px;
}

#locations .card .text {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

#locations .card .row {
    flex: 1;
    display: flex;
    align-items: flex-start;
    gap: 25px;
}

#locations .icon {
    width: 30px;
}

footer {
    background-color: var(--dark-clr);
    color: var(--light-clr);
    text-align: center;
    padding: 5px 0;
}

footer a {
    color: var(--light-pink-clr);
}

footer a:hover {
    color: darkred;
}

/* subpages */
/* menu page */
.above-the-fold {
    text-align: center;
    background-color: var(--dark-clr);
    color: var(--light-pink-clr);
    padding: 50px;
}

.above-the-fold>.inner {
    width: var(--content-width);
    margin: 0 auto;
    line-height: 1.8rem;
}

.above-the-fold h1 {
    margin-bottom: 10px;
}

#ingredients,
#bases {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 25px;
}

#ingredients .img-container,
#bases .grid {
    width: calc(var(--content-width) + 20px);
}

#ingredients .img-container {
    display: grid;
    grid-template-columns: 1.5fr 1fr;
    gap: 25px;
}

#ingredients img {
    width: 100%;
    height: auto;
}

#bases h3 {
    color: var(--accent-clr);
}

#bases .grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 20px;
    padding: 10px;
}

.base {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 25px;
}

.base-img {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    overflow: hidden;
    border: 5px solid var(--accent-clr);
}

.base-img img {
    width: 100%;
    transform: scale(1.2);
